<template lang="html">
  <div class="message-warn-page">
    <!-- 头部 -->
    <default-head header="消息提醒"></default-head>

    <div class="message-box"
      v-infinite-scroll="loadMore"
      :infinite-scroll-disabled="loading"
      :infinite-scroll-distance="10"
      :infinite-scroll-immediate-check="true"
    >
      <info-line :icon="icon" v-for="(item, index) in messageList" :key="item.index">
        <div class="text-box" >
          <div class="text-left">系统消息:</div>
          <div class="text-right">{{item.content}}</div>
        </div>
      </info-line>
      <no-data v-if="!this.messageList.length"></no-data>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import _ from 'underscore'
import minutils from '@/utils/minUtils'
import noData from '@/components/common/noData'
import defaultHead from '@/components/head/defaultHead'
import infoLine from '@/components/common/infoLine'
import message from '@/assets/img/personcenter/message@2x.png'

export default {
  data () {
    return {
      icon: message,
      loading: false,
      pageSize: 11,
      pageIndex: 1,
      pages: 1,
      messageList: []
    }
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  components: { defaultHead, infoLine, noData },
  mounted () {
    this.loadMore()
  },
  methods: {
    loadMore: _.throttle(function () {
      // 关闭无线滚动
      this.loading = true
      if (this.pageIndex === 1) {
        this.getMessageList(this.userId, this.pageIndex++, this.pageSize)
      } else if ((this.pageIndex - 1) * this.pageSize > this.pages) {
        return
      } else {
        this.getMessageList(this.userId, this.pageIndex++, this.pageSize)
      }
    }, 300),
    getMessageList (userId, pageIndex, pageSize) {
      minutils.IndicatorOpen('加载中')
      api.getMessageWarn({userId: userId, pageIndex: pageIndex, pageSize: pageSize}).then((response) => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.pages = result.data.total
          this.messageList = [...this.messageList, ...result.data.messageList]
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.message-warn-page {
  padding-top: .88rem;
}

.text-box {
  overflow: hidden;
  font-size: .32rem;
  .text-left {
    float:left;
    color: @midleColor;
  }
  .text-right {
    float: left;
    margin-left: .2rem;
    color: @lightColor;
  }
}
</style>



